<template>
    <!-- HTML   -->
    <!--     , HTML+   -->

    <div id="app">
        <TodoHeader></TodoHeader>
        <!--   -->
        <!-- inputAddTodo : TodoInput.vue  , appAddTodo : App.vue  -->
        <TodoInput v-on:inputAddTodo="appAddTodo"></TodoInput>
        <!--   -->
        <!-- propsdata : TodoList.vue  , todoItems : App.vue  -->
        <TodoList v-bind:propsdata="todoItems" v-on:listRemoveTodo="appRemoveTodo"></TodoList>
        <TodoFooter v-on:footRemoveAll="appRemoveAll"></TodoFooter>
    </div>
</template>

<script>
    //             import .
    // ES6  : import      .
    import TodoHeader from './components/TodoHeader.vue'
    import TodoInput from './components/TodoInput.vue'
    import TodoList from './components/TodoList.vue'
    import TodoFooter from './components/TodoFooter.vue'

    // ES5  : var      .
    // var TodoHeader = {
    //     template : '<div>header</div>'
    // };

    export default {
        //   
        //     , template, data, methods 
        // export default {}  ES6    .
        
        // todoItems 
        data() {
            return {
                todoItems : []
            }
        },
        //  created()
        created() {
            if(localStorage.length > 0) {
                //          API   .
                for(var i = 0; i < localStorage.length; i++) {
                    this.todoItems.push(localStorage.key(i));
                }
            }
        },
        methods : {
            // TodoInput.vue    value todoItem 
            appAddTodo(todoItem) {
                //    
                localStorage.setItem(todoItem, todoItem);
                this.todoItems.push(todoItem);
            },
            appRemoveTodo(todoItem, index) {
                //   
                localStorage.removeItem(todoItem);
                // splice() :    API.       .
                // todoItems   
                this.todoItems.splice(index, 1);
            },
            appRemoveAll() {
                localStorage.clear();
                this.todoItems = [];
            }
        },
        //   
        components : {
            'TodoHeader' : TodoHeader,
            'TodoInput' : TodoInput,
            'TodoList' : TodoList,
            'TodoFooter' : TodoFooter
        }
    }
</script>

<style>
    /* CSS   */
    /*   HTML  CSS    */

    /* App.vue CSS */
    /*        */
    body {
        text-align : center;
        background-color : #F6F6F8;
    }
    /*      */
    input {
        border-style : groove;
        width : 200px;
    }
    /*     */
    button {
        border-style : groove;
    }
    /*   */
    /* . #  : # id, .  */
    .shadow {
        box-shadow : 5px 10px 10px rgba(0, 0, 0, 0,03)
    }
</style>


