<template>
    <!-- HTML   -->
    <!--     , HTML+   -->

    <section>
        <!-- transition-group :      , tag :   html   -->
        <transition-group name="list" tag="ul">
            <!-- v-for      -->
            <!-- v-for :      HTML   . -->
            <!-- index : v-for     -->
            <!-- ESLint       .  :key="todoItem"   -->
            <!-- :key : v-for     ,             . -->
            <li v-for="(todoItem, index) in propsdata" :key="todoItem" class="shadow">
                <!--   -->
                <i class="checkBtn fas fa-check" aria-hidden="true"></i>
                {{ todoItem }}
                
                <!--     -->
                <!-- @click v-on:click   -->
                <span class="removeBtn" type="button" @click="listRemoveTodo(todoItem, index)">
                    <!--   -->
                    <i class="far fa-trash-alt" aria-hidden="true"></i>
                </span>
            </li>
        </transition-group>
    </section>
</template>

<script>
    export default {
        //   
        //     , template, data, methods 
        // export default {}  ES6    .

        // App.vue propsdata   .
        props : ['propsdata'],

        //      .
        //   propsdata  
        // data() {
        //     return {
        //         todoItems : []
        //     }
        // },
        // //  created()
        // created() {
        //     if(localStorage.length > 0) {
        //         //          API   .
        //         for(var i = 0; i < localStorage.length; i++) {
        //             this.todoItems.push(localStorage.key(i));
        //         }
        //     }
        // },

        methods : {
            listRemoveTodo(todoItem, index) {
                //   
                // localStorage.removeItem(todoItem);   //   
                // splice() :    API.       .
                // todoItems   
                // this.todoItems.splice(index, 1); //   
                this.$emit('listRemoveTodo', todoItem, index);   //   
            }
        }
    }
</script>

<style scoped>
    /* CSS   */
    /*   HTML  CSS    */

    /*   */
    ul {
        list-style-type: none;  /*    */
        padding-left: 0px;
        margin-top: 0;
        text-align: left;
    }
    /*   */
    li {
        display: flex;  /* flex :     */
        min-height: 50px;
        height: 50px;
        line-height: 50px;
        margin: 0.5rem 0;
        padding: 0 0.9rem;
        background: white;
        border-radius: 5px;
    }
    /*  */
    .checkBtn {
        line-height: 45px;
        color: #62acde;
        margin-right: 5px;
    }
    /*  */
    .removeBtn {
        margin-left: auto;
        color: #de4343;
        cursor: pointer;    /*    */
    }
    /*     */
    .list-enter-active, .list-leave-active {
        transition: all 1s;
    }
    /*      */
    .list-enter, .list-leave-to {
        opacity: 0;
        transform: translate(30px);
    }
</style>


