<template>
    <!-- HTML   -->
    <!--     , HTML+   -->

    <!--        -->
    <div class="inputBox shadow">
        <!-- v-model, v-on       . -->
        <!-- placeholder :    , v-on:keyup.enter :         -->
        <input type="text" v-model="newTodoItem" placeholder="Type what you have to do" v-on:keyup.enter="inputAddTodo">
        <!-- <span> : <button>       -->
        <span class="addContainer" v-on:click="inputAddTodo">
            <!-- <i class="addBtn fas fa-plus"> :   +   -->
            <i class="addBtn fas fa-plus" aria-hidden="true"></i>
        </span>

        <!-- v-if : false    HTML     .  -->
        <!-- @close : close  -->
        <modal v-if="showModal" @close="showModal = false" v-on:close="close">
            <!--  Header -->
            <h3 slot="header"></h3>
            <!--  Body -->
            <span slot="body">
                  .
                <!-- <i class="closeModalBtn fas fa-times" aria-hidden="true"></i> -->
            </span>
        </modal>
    </div>
</template>

<script>
    // Modal.vue  
    import Modal from './common/Modal.vue'

    export default {
        //   
        //     , template, data, methods 
        // export default {}  ES6    .

        //     
        data() {
            return {
                newTodoItem : '',
                showModal : false   //    
            }
        },
        //   
        methods : {
            //    .
            //            clear  .
            inputAddTodo() {
                if(this.newTodoItem !==  "") {
                    var value = this.newTodoItem.trim();

                    // setItem()  , .
                    // localStorage.setItem(value, value);  //   
                    this.$emit('inputAddTodo', value);   //   

                    //   (Single Responsibility Principle) :       
                    // this.newTodoItem = '';              
                    this.clearInput();
                }
                else {
                    //     
                    this.showModal = !this.showModal;
                }

            },
            clearInput() {
                this.newTodoItem = '';
            },
            close() {
                'Modal'.close;
            }

            // 
            // addTodo() {
            // !== :   
            //     if(this.newTodoItem !== "") {
            //         var value = this.newTodoItem && this.newTodoItem.trim();
            //         // setItem()  , .
            //         localStorage.setItem(value, value);
            //         this.clearInput();
            //     }
            // }
        },
        components : {
            'Modal' : Modal   // 
        }
    }
</script>

<style scoped>
    /* CSS   */
    /*   HTML  CSS    */

    /*         */
    input:focus {
        outline : tomato;
        outline-style: auto;
    }
    /* inputBox class , ,    ,   */
    .inputBox {
        background : white;
        height : 50px;
        line-height : 50px;
        border-radius : 5px;
    }
    /* inputBox   <input> ,    */
    .inputBox input {
        border-style: none;
        font-size : 0.9rem;
    }
    /*   . , (), ,   */
    .addContainer {
        float : right;
        background : linear-gradient(to right, #6478FB, #8763FB);
        display : block;
        width : 3rem;
        border-radius : 0 5px 5px 0;
        cursor: pointer;    /*    */
    }
    /*  + ,   */
    .addBtn {
        color : white;
        vertical-align : middle;
    }
</style>


