<template>
    <!-- HTML   -->
    <!--     , HTML+   -->


    <div class="clearAllContainer">
        <span class="clearAllBtn" @click="footRemoveAll">Clear All</span>
    </div>
</template>

<script>
    export default {
        //   
        //     , template, data, methods 
        // export default {}  ES6    .

        methods : {
            footRemoveAll() {
                // localStorage.clear();   //   
                this.$emit('footRemoveAll');   //   
            }
        }
    }
</script>

<style scoped>
    /* CSS   */
    /*   HTML  CSS    */

    /* Clear All   */
    .clearAllContainer {
        width: 8.5rem;
        height: 50px;
        line-height: 50px;
        background-color: white;
        border-radius: 5px;
        margin: 0 auto;
        cursor: pointer;    /*    */
    }
    /* Clear All   */
    .clearAllBtn {
        color: #e20303;
        display: black;
    }
</style>


